
*{ 
    margin: 0;padding: 0;box-sizing: border-box;
}
.App{
    display: flex;
    flex-direction: column;
    scroll-behavior: smooth;
    height: 100vh;
}
.loadinglogo{
    position: absolute;
    top:15%;
    left:35%;
    text-align: center;
}
.loadinglogo img{
    width: 300px;
    animation:animate 5s infinite linear ;
    height: 300px;  
}
@keyframes animate{
    from {
        transform: rotateY(0deg);
    }
    to{
        transform: rotateY(360deg);
    }
    
}