@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;1,400;1,700&display=swap);
@font-face{
  font-family:'iCielMedium';
  src: local('iCielMedium'),url(/static/media/iCiel-Medium.b23bb62d.ttf) format('truetype');
  font-weight: normal
}

body {
  margin: 0;
  font-family:'iCielMedium', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{ 
    margin: 0;padding: 0;box-sizing: border-box;
}
.App{
    display: flex;
    flex-direction: column;
    scroll-behavior: smooth;
    height: 100vh;
}
.loadinglogo{
    position: absolute;
    top:15%;
    left:35%;
    text-align: center;
}
.loadinglogo img{
    width: 300px;
    animation:animate 5s infinite linear ;
    height: 300px;  
}
@keyframes animate{
    from {
        transform: rotateY(0deg);
    }
    to{
        transform: rotateY(360deg);
    }
    
}
nav{
    margin:20px;
    font-size:14px;
    font-weight: 500;
    display: flex;
    width: 100vw;
    background:none;
    position: fixed;
    z-index: 1000;
    top:0;
    left:0;
    /*color:#393c41;*/
    justify-content: space-between;
    color:#393c41;;
}
.logo img{
    width: 100px;
    height: 15px;
}
.navbar-center a{
    text-decoration: none;
    margin-right: 25px;
    cursor:pointer;
    color:#393c41;
}
nav .navbar-center{
    display: flex;
    margin-left: 100px;
    justify-content: center;
    text-transform: uppercase;
    text-align: center;
}
nav .navbar-end{
    display: flex;
    margin-right: 40px;
       text-transform: uppercase;
    justify-content: flex-end;
}
.navbar-end p{
    margin-right: 10px;
}
.models_grid{
    width: 100%;
    height: auto;
    background-color:black;
    display:flex;
    padding:5% 10%;
    flex-direction:column;
    color:white;
}
.models_row{
     display:flex;
     flex-direction: row;
     width:100%;
}
.models_box{
    position: relative;
    flex-basis:50%;
    height:400px;
}
.models_imgbox{
    position: relative;
    width: 100%;
    height: 100%;
}
.models_imgbox img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit:cover;
}
.models_textright{
    flex-basis: 50%;
    padding: 60px;
}
.models_row_reverse{
    display:flex;
    flex-direction: row-reverse;
    width:100%;
}
.models_row_reverse .models_box{
    position: relative;
    flex-basis: 50%;
    height:400px;
}
.models_row_reverse .models_textright{
    flex-basis: 50%;
    padding: 60px;
    font-family: Poppins;
    line-height:20px;
}
.models_textright h3{
    margin-bottom: 15px;
    font-size:2em;
}
.models_textright p{
    font-size:1em;
    font-weight:lighter;
}
.gaz-container{
    width:100%;
