    background-position:center;
    text-align:center;
    padding:110px;
    scroll-snap-align: start;
}
.title{
    font-size:2.5em;
    font-weight:500;
    color:#393c41;
}
.description a{
     max-width: 150px;
     font-size: 13px;
     text-decoration: none;
     border-bottom: 1px solid #393c41;;
     color:#393c41;
     opacity: 0.7;
     cursor: pointer;
     transition: all 0.1s ease-out ;
}
.description a:hover{
    opacity:1;
    border-bottom: 2px solid #5c5e62;
}
.btn-one{
    padding: 13px 45px;
