.down{
    animation:down 3s infinite;
}
@keyframes down{
     0%{
         transform :translateY(0);
     }
     25%{
         transform :translateY(10px);
     }
     50%{
        transform :translateY(0);
    }
    75%{
        transform :translateY(10px);
    }
}
